.checkoutProduct {
  display: flex;
  margin-top: 20px;
  margin-bottom: 20px;
  background-color: white;
  padding-left: 20px;
  /* width: 96%; */
  align-items: center;
  /* margin-left: auto;
  margin-right: auto; */
}

.checkoutProduct_image {
  object-fit: contain;
  width: 180px;
  height: 180px;
  padding-right: 20px;
}

.checkoutProduct_info {
  padding-left: 20px;
  max-width: 70%;
}

.checkoutProduct_info > button {
  background-color: #f0c14b;
  border: 1px solid;
  border-color: #a88734;
  color: #111;
  margin-top: 15px;
}

.checkoutProduct_rating {
    display: flex;
}

.checkoutProduct_title {
    font-size: 16px;
    font-weight: 400;
    /* padding-top: 10px; */
}
