.login_logo {
  width: 100px;
  object-fit: contain;
  margin-top: 20px;
  margin-bottom: 20px;
}

.login {
  background-color: #fff;
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.login_container {
  width: 300px;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid lightgray;
}

.login_container > h1 {
  margin-bottom: 20px;
  font-weight: 500;
}

.login_container > form > input {
  height: 30px;
  margin-bottom: 10px;
  background-color: #fff;
  width: 98%;
}

.login_container > p {
  font-size: 12px;
  margin-top: 15px;
}

.login_signInButton {
  background-color: #f0c14b;
  border-color: #a88734;
  border: 1px solid;
  border-radius: 2px;
  height: 30px;
  width: 100%;
  margin-top: 10px;
}

.login_registerButton {
  /* background-color: ; */
  border-color: darkgray;
  border: 1px solid;
  border-radius: 2px;
  height: 30px;
  width: 100%;
  margin-top: 10px;
}
