.header {
  background-color: #131921;
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  z-index: 100;
}

.header_logo {
  width: 100px;
  margin: 0 20px;
  margin-top: 18px;
  object-fit: contain;
}
.header_logo:hover{
  cursor: pointer;
}

.header_search {
  display: flex;
  align-items: center;
  flex: 1;
}

.header_searchInput {
  height: 12px;
  padding: 10px;
  border: none;
  width: 100%;
}

.header_searchIcon {
  background-color: #cd9042;
  padding: 5px;
  height: 22px !important ;
}

.header_nav {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.header_option {
  color: white;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  margin: 0 10px;
}
.header_option :hover {
  cursor: pointer;
  text-decoration: none;
  
}
.span1 {
  font-size: 10px;
  font-weight: normal;
}
.span2 {
  font-size: 14px;
  font-weight: 700;
}

.header_basket {
  color: white;
  text-decoration: none;
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 0 10px;
  font-size: larger;
}
.header_basket :hover {
  cursor: pointer;
}
.basketCount{
  margin-left: 5px;
  margin-right: 10px;
  font-size: larger;
}